@media (max-width: 575.98px) { 
  .CodeMirror {
    border: 1px solid #eee;
    height: 80vh;
    /* width: 1300px; */
    width: 99vw;
    resize: both;
    
  }
  .flex-container1 {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }
  .flex-container2 {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }

  .item21 {
    align-self: left;
    flex: 1 1;
    /* height: 7.5vh; */
    height: 15vh;
    width: 50vw;
  }
  
  .item22 {
    align-self: right;
    flex: 1 1;
    /* height: 7.5vh; */
    height: 15vh;
    width: 49vw;
  }
  
  .item23 {
    flex: 100% 1;
    /* height: 15vh; */
    height: 30vh;
    width: 99vw;
  }

  h1{
    text-align: left;
  }
}

/* // Small devices (landscape phones, 576px and up) */
@media (min-width: 576px) and (max-width: 767.98px) { 
  .CodeMirror {
    border: 1px solid #eee;
    height: 80vh;
    /* width: 1300px; */
    width: 99vw;
    resize: both;
  }
  .flex-container1 {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }
  .flex-container2 {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }

  .item21 {
    align-self: left;
    flex: 1 1;
    height: 7vh;
    width: 50vw;
  }
  
  .item22 {
    align-self: right;
    flex: 1 1;
    height: 7vh;
    width: 49vw;
  }
  
  .item23 {
    flex: 100% 1;
    height: 15vh;
    width: 99vw;
  }
  h1{
    text-align: left;
  }
}

